import React, { useState } from 'react'
import { Modal, Button, Form } from 'react-bootstrap'
import ValidationMessage from './validationMessage'

import DualListBox from 'react-dual-listbox'

const UPdateRoleModal = (props) => {

  const { name, description, profileList } = props.roleData

  const isDisabled = props.isUpdating || props.isUpdateSuccess

  const options = props.profiles.map( (profile) => {
    return {
      value: profile.profileId.toString(),
      label: profile.name
    }
  } )

  const selectedArray = profileList.map( (profileReq) => {
    return props.profiles.filter(profile => profile.profileId === profileReq.profileId)
  } )

  const optionsSelected = selectedArray.map( (profile) => profile[ 0 ].profileId.toString());


  const [ selected, setSelected ] = useState( optionsSelected )

  const validateName = (value) => {
    let error
    if (!value) {
      error = 'Campo requerido'
    }
    return error
  }

  const onSubmit = async (values, formikProps) => {
    const profileList = selected.map( id => {
      return {
        profileId: parseInt( id )
      }
    } )
    const addValues = {
      rolId: props.roleId,
      profileList
    }
    props.updateRole( addValues, props.roleId )
  }

  const onChange = selected => setSelected(
    selected
  )

  return (
    <Modal
      show={ props.showModal }
      onHide={ () => props.toggleUpdateModal() }
      centered
    >
      <Form
        onSubmit={ (e) => {
          e.preventDefault()
          handleSubmit()
        } }
      >
        <Modal.Header closeButton>
          <Modal.Title as="h5">Actualizar Rol</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group>
            <Form.Label>Nombre</Form.Label>
            <Form.Control
              id="name"
              name="name"
              type="text"
              className="form-control"
              validate={ validateName }
              disabled={ isDisabled }
            />

            {/*{ errors.name && touched.name && (*/}
            {/*  <ValidationMessage message={ errors.name } />*/}
            {/*) }*/}
          </Form.Group>

          <Form.Group>
            <Form.Label>Descripción</Form.Label>
            <Form.Control
              className="form-control"
              component="textarea"
              name="description"
              rows="6"
              disabled={ isDisabled }
            />
          </Form.Group>
          <Form.Group controlId="exampleForm.ControlTextarea1">
            <Form.Label>Perfiles</Form.Label>
            <DualListBox
              options={ options }
              selected={ selected }
              onChange={ onChange }
              disabled={ isDisabled }
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="secondary"
            onClick={ () => props.toggleUpdateModal() }
            disabled={ isDisabled }
          >
            Cancelar
          </Button>
          <Button disabled={ isDisabled } type="submit" variant="primary">
            Guardar
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  )
}

export default UPdateRoleModal
