import React, { useState } from 'react'
import { Modal, Button, Form } from 'react-bootstrap'
import ValidationMessage from './validationMessage'

import DualListBox from 'react-dual-listbox'


const AddRoleModal = (props) => {
  const [ selected, setSelected ] = useState( [] )

  const isDisabled = props.isAdding || props.isAddSuccess

  const options = props.profiles.map( (profile) => {
    return {
      value: profile.profileId.toString(),
      label: profile.name
    }
  } )

  const validateName = (value) => {
    let error
    if (!value) {
      error = 'Campo requerido'
    }
    return error
  }

  const onSubmit = async (values, formikProps) => {
    const profileList = selected.map( (id) => {
      return {
        profileId: parseInt( id )
      }
    } )
    const addValues = {
      ...values,
      profileList
    }
    props.addRole( addValues )
  }

  return (
    <Modal
      show={ props.showModal }
      onHide={ () => props.toggleAddModal() }
      centered
    >
      <Form
        onSubmit={ (e) => {
          e.preventDefault()
          handleSubmit()
        } }
      >
        <Modal.Header closeButton>
          <Modal.Title as="h5">Añadir Rol</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group>
            <Form.Label>Nombre</Form.Label>
            <Form.Control
              id="name"
              name="name"
              type="text"
              className="form-control"
              validate={ validateName }
              disabled={ isDisabled }
            />

            {/*{ errors.name && touched.name && (*/}
            {/*  <ValidationMessage message={ errors.name } />*/}
            {/*) }*/}
          </Form.Group>

          <Form.Group>
            <Form.Label>Descripción</Form.Label>
            <Form.Control
              className="form-control"
              component="textarea"
              name="description"
              rows="6"
              disabled={ isDisabled }
            />
          </Form.Group>
          <Form.Group controlId="exampleForm.ControlTextarea1">
            <Form.Label>Perfiles</Form.Label>
            <DualListBox
              options={ options }
              selected={ selected }
              onChange={ setSelected }
              disabled={ isDisabled }
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="secondary"
            onClick={ () => props.toggleAddModal() }
            disabled={ isDisabled }
          >
            Cancelar
          </Button>
          <Button disabled={ isDisabled } type="submit" variant="primary">
            Guardar
          </Button>
        </Modal.Footer>
      </Form>

    </Modal>
  )
}

export default AddRoleModal
