import React from 'react'
import { Row, Col, Button, Card } from 'react-bootstrap'

import styled from 'styled-components'

const Icon = styled.i`
  margin-right: 10px;
`
const ActionBar = styled.div`
margin-bottom: 10px;
text-align: right`


const actionBar = (props) => {
  return (
    <ActionBar>
      <Row>
        { props.addView ? (
          <Col>
            <Card.Title as='h5'>Añadir Rol</Card.Title>
          </Col>
        ) : (
          <Col>
            <Button onClick={ props.handleView }>
              <Icon className="feather icon-plus" />
              Añadir Rol
            </Button>
          </Col>
        ) }
      </Row>
    </ActionBar>
  )
}

export default actionBar
