import React from 'react'
import AnimatedTree from 'react-animated-tree'
import styled from 'styled-components'

const TreeWrapper = styled.div`
  padding-left: 1rem;
  margin-left: 2rem;
  margin-bottom: 30px;
  border-left: 3px solid #0077ac !important;
`

const Icon = styled.i`
  color: #0077ac !important;
  cursor: pointer;
  font-size: 16px;
`

const EnabledIcon = styled.i`
  color: #008a60 !important;
  font-size: 16px;
`

const treeView = (props) => {
  const treeStyles = {
    position: 'inherit',
    top: 40,
    left: 40,
    color: '#888',
    fill: '#000',
    width: '100%'
  }
  const typeProfile = <Icon className="feather icon-list align-middle" />
  const typeModule = <Icon className="feather icon-layers align-middle" />
  const typeEnabledAction = (
    <EnabledIcon className="feather icon-check align-middle" />
  )

  /*const typeDisabledAction = (
    <DisabledIcon className="feather icon-x align-middle" />
  );*/

  return (
    <TreeWrapper>
      <h6>Perfiles y permisos</h6>

      { props.row.profileList.length > 0 ? (
        props.row.profileList.map( (profile) => {
          return (
            <AnimatedTree
              key={ profile.profileId }
              content={ profile.name }
              type={ typeProfile }
              open
              style={ treeStyles }
            >
              { profile.moduleList.map( (module) => {
                return (
                  <AnimatedTree
                    key={ module.moduleId }
                    content={ module.name }
                    type={ typeModule }
                    open
                    style={ treeStyles }
                  >
                    { module.actionList.map( (action) => {
                      return (
                        <AnimatedTree
                          key={ action.actionId }
                          type={ typeEnabledAction }
                          content={ action.name }
                        />
                      )
                    } ) }
                  </AnimatedTree>
                )
              } ) }
            </AnimatedTree>
          )
        } )
      ) : (
        <h6>No existen permisos asociados</h6>
      ) }
    </TreeWrapper>
  )
}

export default treeView
