import React from 'react'
import { Modal, Button } from 'react-bootstrap'

const ConfirmDeleteModal = (props) => {
  const isDisabled = props.isDeleting || props.isDeleteSuccess
  return (
    <Modal
      show={ props.showModal }
      onHide={ () => props.toggleDeleteModal() }
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title as="h5">Confirmación</Modal.Title>
      </Modal.Header>
      <Modal.Body><h6>Está seguro que desea eliminar el Rol?</h6></Modal.Body>
      <Modal.Footer>
        <Button
          disabled={ isDisabled }
          variant="secondary"
          onClick={ () => props.toggleDeleteModal() }
        >
          Cancelar
        </Button>
        <Button
          disabled={ isDisabled }
          onClick={ () => props.deleteRole( props.roleId ) }
          variant="primary"
        >
          Eliminar
        </Button>
      </Modal.Footer>
    </Modal>
  )
}

export default ConfirmDeleteModal
