import React from 'react'
import style from './styles.css'
import { Row, Col, Card } from 'react-bootstrap'
import BootstrapTable from 'react-bootstrap-table-next'
import _ from 'lodash'


/* Page Components */
import TreeView from './Components/treeView'
import ActionBar from './Components/actionBar'
import ActionRow from './Components/actionColumn'
import ExpandIcon from './Components/expandIcon'
import NameColumn from './Components/nameColum'
import DescriptionColumn from './Components/descriptionColumn'
import ConfirmDeleteModal from './Components/confirmDeleteRoleModal'
import AddRoleModal from './Components/addRoleModal'
import UpdateRoleModal from './Components/updateRoleModal'

const columns = [
  {
    dataField: 'name',
    text: 'Rol',
    style: {
      whiteSpace: 'pre-line',
      verticalAlign: 'middle'
    }
  },
  {
    dataField: 'description',
    text: 'Descripción',
    style: {
      verticalAlign: 'middle',
      whiteSpace: 'pre-line'
    }
  },
  {
    dataField: 'actions',
    text: 'Acciones',
    style: {
      verticalAlign: 'middle'
    }
  }
]


class RoleManagement extends React.Component {
  state = {
    openDeleteRoleModal: false,
    openAddRoleModal: false,
    openUpdateRoleModal: false,
    showLoader: true,
    rolesData: null,
    roleData: null,
    roleId: null,
    currentRole: null,
    modalContent: null
  }

  componentDidMount() {
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (!_.isEqual( nextProps.roles, prevState.rolesData )) {
      return {
        rolesData: nextProps.roles
      }
    }

    if (!_.isEqual( nextProps.role, prevState.roleData )) {
      return {
        roleData: nextProps.role
      }
    }

    return null
  }

  toggleModal = (modalContent) => {
    this.setState( (prevState) => ( {
      showLoader: prevState.openDeleteRoleModal,
      openDeleteRoleModal: !prevState.openDeleteRoleModal
    } ) )
    this.props.reset()
  }

  toggleDeleteModal = () => {
    this.setState( (prevState) => ( {
      showLoader: prevState.openDeleteRoleModal,
      openDeleteRoleModal: !prevState.openDeleteRoleModal
    } ) )
    this.props.reset()
  }

  toggleAddModal = () => {
    this.setState( (prevState) => ( {
      showLoader: prevState.openAddRoleModal,
      openAddRoleModal: !prevState.openAddRoleModal
    } ) )

    this.props.reset()
  }

  toggleUpdateModal = (roleId) => {
    if (!_.isNil( roleId )) {
      this.props.fetchRole( roleId )
    }
    this.setState( (prevState) => ( {
      showLoader: prevState.openUpdateRoleModal,
      openUpdateRoleModal: !prevState.openUpdateRoleModal,
      roleData: null
    } ) )
    this.props.reset()
  }

  setCurrentId = (roleId) => {
    this.setState( { roleId } )
  }

  expandRow = {
    renderer: (row) => (
      <div>
        <TreeView row={ row } />
      </div>
    ),
    showExpandColumn: true,
    expandByColumnOnly: true,
    expandHeaderColumnRenderer: ({ isAnyExpands }) => {
      return false
    },
    expandColumnRenderer: ({ expanded }) => {
      return <ExpandIcon expanded={ expanded } />
    }
  }

  render() {
    const rolesData =
      this.state.rolesData.length > 0
        ? this.state.rolesData.map( (role) => {
          return {
            id: role.rolId,
            profileList: role.profileList,
            name: <NameColumn name={ role.name } />,
            description: <DescriptionColumn description={ role.description } />,
            actions: (
              <ActionRow
                roleId={ role.rolId }
                setCurrentId={ this.setCurrentId }
                openUpdateModal={ this.toggleUpdateModal }
                openDeleteModal={ this.toggleDeleteModal }
              />
            )
          }
        } )
        : []
    return (
      <div>
        <ConfirmDeleteModal
          roleId={ this.state.roleId }
          toggleDeleteModal={ this.toggleDeleteModal }
          showModal={ this.state.openDeleteRoleModal }
          deleteRole={ this.props.deleteRole }
          isDeleting={ this.props.isDeleting }
          isDeleteSuccess={ this.props.isDeleteSuccess }
          isDeleteError={ this.props.isDeleteError }
        />

        <AddRoleModal
          toggleAddModal={ this.toggleAddModal }
          showModal={ this.state.openAddRoleModal }
          profiles={ this.props.profiles }
          addRole={ this.props.addRole }
          isAdding={ this.props.isAdding }
          isAddError={ this.props.isAddError }
          isAddSuccess={ this.props.isAddSuccess }
        />
        { this.props.role && !_.isEmpty( this.props.role ) ? (
          <UpdateRoleModal
            roleId={ this.state.roleId }
            roleData={ this.props.role }
            toggleUpdateModal={ this.toggleUpdateModal }
            showModal={ this.state.openUpdateRoleModal }
            profiles={ this.props.profiles }
            updateRole={ this.props.updateRole }
            isUpdating={ this.props.isUpdating }
            isUpdateSuccess={ this.props.isUpdateSuccess }
            isUpdateError={ this.props.isUpdateError }
          />
        ) : null }
        <ActionBar handleView={ this.toggleAddModal } />
        <Row>
          <Col>
            <div className="role-container-main">
              <div className="role-container-header">
                <h5>Lista de roles</h5>
              </div>
              <div className="task-data">
                <BootstrapTable
                  keyField="id"
                  data={ rolesData }
                  columns={ columns }
                  bordered={ false }
                  expandRow={ this.expandRow }
                />
              </div>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}

export default RoleManagement
